 <template>
    <div class="alet_container">
	    <section class="tip_text_container">
            <div class="tip_icon">
                <span></span>
                <span></span>
            </div>
            <p class="tip_text">{{alertText}}</p>
            <div class="confirm" @click="closeTip">确认</div>
        </section>

        <div class="alert_mask"></div>
    </div>
</template>

<script>
export default {
	data(){
        return{
            positionY: 0,
            timer: null,
        }
    },
    mounted(){
  
    },
    props: ['alertText'],
    methods: {
        closeTip(){
            this.$emit('closeTip')
        }
    }
}
</script>

<style lang="less" scoped>

@keyframes tipMove{
   0%   { transform: scale(1) }
   35%  { transform: scale(.8) }
   70%  { transform: scale(1.1) }
   100% { transform: scale(1) }
}

.alet_container{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;



    // z-index: 999;
    // background: #000;
    // opacity: .2;
.alert_mask{

    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #000;
    opacity: .2;

}



    .tip_icon{
        width: 3rem;
        height: 3rem;
        border: 0.15rem solid #f8cb86;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;



        span:nth-child(1){
            width: 0.12rem;
            height: 1.5rem;
            background-color: #f8cb86;
        }

        span:nth-child(2){

            width: 0.2rem;
            height: 0.2rem;
            border: 1px;
            border-radius: 50%;
            margin-top: .2rem;
            background-color: #f8cb86;

        }



    }
    .tip_text{

        font-size: 0.7rem;
        color: #333;
        line-height: .9rem;
        text-align: center;
        margin-top: .8rem;
        padding: 0 .4rem;
    }
    .confirm{

        font-size: 0.8rem;
        color: #fff;
        font-weight: bold;
        margin-top: .8rem;
        background-color: #4cd964;
        width: 100%;
        text-align: center;
        line-height: 1.8rem;
        border: 1px;
        border-bottom-left-radius: 0.25rem;
        border-bottom-right-radius: 0.25rem;
    }
}

.tip_text_container{

    box-shadow:1px 1px 2px rgba(0,0,0,.1);

    position: absolute;
    z-index:999;
    top: 50%;
    left: 50%;
    margin-top: -6rem;
    margin-left: -6rem;
    width: 12rem;
    -webkit-animation: tipMove .4s;
    animation: tipMove .4s;
    background-color: white;
    border: 1px;
    padding-top: .6rem;

    display: flex;
    
    justify-content: center;
    
    align-items: center;
    

    flex-direction: column;
    border: 1px;
    border-radius: 0.25rem;

}



